import QueryBuilder from '../classess/QueryBuilder'

class Filter {
  _id // id 
  topic // id of topic
  level // id of level
  question // string of question
  category // string of category
  isMCQ // boolean
  createdBy // id of user
  deleted // boolean

  constructor(params) {
    this._id = params._id
    this.topic = params.topic
    this.level = params.level
    this.question = params.question
    this.isMCQ = params.isMCQ
    this.createdBy = params.createdBy
    this.deleted = params.deleted;
    this.category = params.category;
  }

  buildQuery = () => {
    let query = new QueryBuilder('')
    if(this._id) {
      query.set('_id', this._id);
    }
    if(this.topic) {
      query.set('topic', this.topic);
    }
    if(this.level) {
      query.set('level', this.level);
    }
    if(this.question) {
      query.set('question', this.question);
    }
    if(this.isMCQ) {
      query.set('isMCQ', this.isMCQ);
    }
    if(this.createdBy) {
      query.set('createdBy', this.createdBy);
    }
    if(this.deleted) {
      query.set('deleted', this.deleted);
    }
    if(this.category) {
      query.set('category', this.category);
    }

    var queryStr = query.get();
    return queryStr;
  }
}

export default Filter;