import { history } from "../../../history";
import * as Types from "../types";
import { Axios } from "../root"
import { displaySuccessMessage, displayServerErrorMessage, displayInfo } from '../../../service/Alert';
import config from "../../../configs/config";
import { subRooter  } from "../../../configs/vars";
import Filter from "./filterClass";

export const getData = (params) => {
  if(params === null) {
    params = {}
    params.page     = 1
    params.perPage  = 4
  }
  return async (dispatch, getState) => {
    await Axios.get(`/assessments?page=${params.page}&perPage=${params.perPage}`).then(response => {
      dispatch({
        type: Types.FETCH_ASSESSMENTS,
        data: response.data.assessments,
        totalPages: response.data.totalPages,
        params
      })
    })
  }
}

export const addData = (obj) => {
  return (dispatch, getState) => {
    let params = getState().assessment.params;
    let videos = obj.videos;

    let formData = new FormData();
    formData.append('question', obj.question);
    formData.append('level', obj.level);
    formData.append('youtubeUrl', obj.youtubeUrl);
    formData.append('videos', videos[0]);
    formData.append('description', obj.description);
    formData.append('image', obj.image[0]);
    formData.append('topic', obj.topic);
    formData.append('isMCQ', obj.isMCQ);
    formData.append('MCQs', JSON.stringify(obj.MCQs));
    formData.append('mark', obj.mark);

    Axios
      .post("/assessment/create", formData)
      .then(response => {
        dispatch({ type: Types.ADD_ASSESSMENT, obj: response.data.doc })
        dispatch(getData(params));
        displaySuccessMessage('Successfully uploaded new question!')
        history.push(`${subRooter}/assessment`)
      })
      .catch(displayServerErrorMessage)
  }
}

export const getInitialData = () => {
  return async (dispatch, getState) => {
    await Axios.get(`/assessments`).then(response => {
      dispatch({ type: Types.FETCH_ALL_ASSESSMENTS, data: response.data.assessments })
    })
  }
}

export const filterData = (value, filterOption) => {
  return dispatch => dispatch({ type: Types.FILTER_ASSESSMENT , value, filterOption })
}

export const updateData = obj => {
  return (dispatch, getState) => {
    let params = getState().assessment.params;

    let formData = new FormData();
    formData.append('question', obj.question);
    formData.append('youtubeUrl', obj.youtubeUrl);
    formData.append('videos', obj.videos[0]);
    formData.append('description', obj.description);
    formData.append('level', obj.level);
    formData.append('image', obj.image[0]);
    formData.append('topic', obj.topic);
    formData.append('isMCQ', obj.isMCQ);
    formData.append('MCQs', JSON.stringify(obj.MCQs));
    formData.append('mark', obj.mark);

    Axios
      .put("/assessment/" + obj._id, formData)
      .then(response => {
        dispatch({ type: Types.UPDATE_ASSESSMENT, obj })
        dispatch(getData(params));
        displaySuccessMessage('Successfully updated!')
      })
      .catch(displayServerErrorMessage)
  }
}

export const getDatabyFilter = (params) => {
  return async (dispatch, getState) => {
    let filter = new Filter(params);
    let query = filter.buildQuery();
    await Axios.get(`/assessments/filter` + query).then(response => {
      dispatch({
        type: Types.FILTER_ASSESSMENT_BY_QUERY,
        data: response.data.assessments,
      })
    })
  }
}

export const getMyData = (params) => {
  return async (dispatch) => {
    let filter, query = "";

    if(params) {
      filter = new Filter(params);
      query = filter.buildQuery(params);
    }
    await Axios.get(`/tutor/assessments${query}`)
      .then(response => {
        dispatch({
          type: Types.FETCH_ALL_MY_ASSESSMENT,
          data: response.data.assessments,
        })
      })
  }
}

export const deleteData = obj => {
  return dispatch => {
    
    Axios
      .delete("/assessment/" + obj._id)
      .then(response => {
        dispatch({ type: Types.DELETE_ASSESSMENT, obj })
      })
      .catch(displayServerErrorMessage)
  }
}
export const deleteDataByIds = ids => {
  return dispatch => {
    
    Axios
      .put("/assessment-delete", {
        ids
      })
      .then(response => {
        const deletedIds  = response.data.deletedIds
        const relatedData = response.data.relatedData

        if(deletedIds.length > 0) {
          displaySuccessMessage(`Successfully deleted ${deletedIds.length} data!`)
        }
        if(relatedData.length > 0) {
          displayInfo(`${relatedData.length} data ${relatedData.length > 1 ? "have" : "has"} relevant data.`);
        }

        dispatch({ type: Types.DELETE_ASSESSMENTS, ids: deletedIds })
      })
      .catch(displayServerErrorMessage)
  }
}
