import React, { Component } from 'react'
import {
  Card,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label,
  InputGroup,
	InputGroupAddon,
} from "reactstrap";
import Breadcrumbs from "../../../components/@vuexy/breadCrumbs/BreadCrumb";
import Checkbox from "../../../components/@vuexy/checkbox/CheckboxesVuexy"
import BasicDropZone from "../../../components/Wrappers/BasicDropZone"
import { Check, Mic } from "react-feather"
import Editor from '../../../components/Wrappers/Editor'
import AudioRecorder from '../../../components/Wrappers/AudioRecorder'
import {subRooter} from "../../../configs/vars";
import * as Feather from "react-feather";
import Select from "react-select"
import { Link } from "react-router-dom";
import { connect } from 'react-redux';

import { getInitialData as getLevels } from "../../../redux/actions/level"
import { getParentData as getCategories} from "../../../redux/actions/category"
import { getDataByType as getTopics } from "../../../redux/actions/topic"
import { getQueryStringValue } from "../../../service/helper";
import { addData as addAssessment, updateData, getDatabyFilter } from "../../../redux/actions/assessment"

const markOptions = [
  { value: 1, label: "1" },
  { value: 2, label: "2" },
  { value: 3, label: "3" },
  { value: 4, label: "4" },
  { value: 5, label: "5" },
  { value: 10, label: "10" },
]

export class AssessmentForm extends Component {

  static getDerivedStateFromProps(props, state) {
    if(props.filteredData[0] &&!Object.is(props.filteredData[0], state.currentData)) {
      let currentData = props.filteredData[0];

      return {
        id: currentData._id,
        currentData: currentData,
				question: currentData.question,
				youtubeUrl: currentData.youtubeUrl,
        MCQs: currentData.MCQs,
				description: currentData.description, // html content
				selectedCategoryId: currentData.topic.categoryId._id,
				selectedTopicId: currentData.topic._id,
        selectedLevelId: currentData.level._id,
        isMCQ: currentData.isMCQ,
        currentData: currentData,
        mark: currentData.mark
      }
    }
    return null
  }

	state = {
		question: '',
		youtubeUrl: '',
		uploadedImages: [],
		uploadedVideos: [],
    MCQs: [
      {
        index: 0,
        isCorrect: false,
        text: ''
      }
    ],
    isMCQ: false,
    description: "",
    mark: 1,
    isInvalid: false,

    // edit
    selectedLevelId: '',
    selectedCategoryId: '', 
    selectedTopicId: '',
    currentData: null,
  }
  formtype = ""
  
  componentDidMount() {
    this.props.getLevels()
    this.props.getCategories()
    this.props.getTopics('ASSESSMENT')
    this.formtype = getQueryStringValue('type');
		let id = getQueryStringValue('id');
    // let currentData = this.props.allAssessments.find((elt) => elt._id === id)

    if(id !== "") {
      this.props.getDatabyFilter({_id: id});
    } else {
      this.initdata();
    }
  }

  initdata = () => {
    this.setState({
      question: '',
      youtubeUrl: '',
      uploadedImages: [],
      uploadedVideos: [],
      MCQs: [
        {
          index: 0,
          isCorrect: false,
          text: ''
        }
      ],
      isMCQ: false,
      description: "",
      mark: 1,
      isInvalid: false,

      // edit
      selectedLevelId: '',
      selectedCategoryId: '', 
      selectedTopicId: '',
      currentData: null,
    })
  }

	handleOnSubmit = (event) => {
    event.preventDefault();
    console.log("this.state", this.state);

    if (this.isValid(this.state, this.formtype)) {
      this.setState({
        isInvalid: false
      })
			if(this.formtype !== 'edit') {
				this.props.addAssessment({
					question: this.state.question,
					level: this.state.selectedLevelId,
					youtubeUrl: this.state.youtubeUrl,
					image: this.state.uploadedImages,
					videos: this.state.uploadedVideos,
					MCQs: this.state.MCQs,
					description: this.state.description,
          topic: this.state.selectedTopicId,
          isMCQ: this.state.isMCQ,
          mark: this.state.mark,
				})
			} else {
				this.props.updateData({
					_id: this.state.id,
					question: this.state.question,
					level: this.state.selectedLevelId,
					youtubeUrl: this.state.youtubeUrl,
					image: this.state.uploadedImages,
					videos: this.state.uploadedVideos,
					MCQs: this.state.MCQs,
					description: this.state.description,
          topic: this.state.selectedTopicId,
          isMCQ: this.state.isMCQ,
          mark: this.state.mark,
				})
			}
		} else {
      this.setState({
        isInvalid: true
      })
    }
  }
  
  isValid = (state, type) => {
		if(type === 'edit') {
			return state.question && state.selectedCategoryId && state.selectedTopicId && state.selectedLevelId
		} else {
			return state.question && state.selectedCategoryId && state.selectedTopicId && state.selectedLevelId
		}
	}

	handleFiles = (type, files) => { // type = string
		if(type === 'image') {
			this.setState({
				uploadedImages: files
			})
		} else if(type === 'video') {
			this.setState({
				uploadedVideos: files
			})
		}
	}

	handleEditor = (strAsHtml) => {
    if(!Object.is(this.state.description, strAsHtml)) {
      this.setState({
        description: strAsHtml
      })
    }
	}

	handleChange = (name, type) => {
		this.setState({
			[name]: type
		})
  }
  
  addNewMCQ = () => {
    let MCQs = this.state.MCQs;
    const newMCQ = {
      index: MCQs.length,
      isCorrect: false,
      text: ''
    }
    MCQs.push(newMCQ);
    this.setState({
      MCQs: MCQs.slice()
    })
  }

  handelChangeMCQValueByIndex = (index, value) => {
    if(typeof index === 'undefined') {
      alert("You need to pass index")
    }
    let mcqs = this.state.MCQs;
    mcqs[index].text = value;

    this.setState({
      MCQs: mcqs
    })
  }

  handleDeleteMCQByIndex = (index) => {
    let newMCQs = this.state.MCQs.filter((mcq, i) => 
      index  !== i
    );

    this.setState({
      MCQs: newMCQs
    })
  }

  checkMCQCorrectAnswer = (index) => {
    let mcqs = this.state.MCQs;
    mcqs[index].isCorrect = !mcqs[index].isCorrect;

    this.setState({
      MCQs: mcqs
    })
  }

	renderCategory = (categories) => {
		let result = []
		for (const category of categories) {
			result.push({
				value: category._id,
				label: category.name,
				isDisabled: category.children && category.children.length > 0,
			});

			if(category.children) {
				for (const child of category.children) {
					result.push({
						value: child._id,
						label: <span style={{paddingLeft: '3px'}} className='pl-1'>{child.name}</span>,
					});
				}
			}
		}
		return result;
	}

	render() {
    const { 
      MCQs, 
      isMCQ,
      mark, 
      description, 
      selectedLevelId,
			selectedCategoryId, 
      selectedTopicId,
      question,
      youtubeUrl,
      currentData
    } = this.state;
    const {
      topics, levels, categories
    } = this.props;

    if(MCQs.length === 0) {
			this.addNewMCQ();
    }

    let subjectOptions = topics.filter((topic) => {
			return topic.levelId._id === selectedLevelId && topic.categoryId._id === selectedCategoryId
		}).map((elt) => ({value: elt._id, label: elt.name}));
    
    const levelOptions = levels.map((level) => ({
      value: level._id,
      label: level.name
    }));

    const categoryOptions = this.renderCategory(categories);
    
		return (
      <React.Fragment>
        <Breadcrumbs
          breadCrumbTitle="Assessment"
          breadCrumbParent="Area"
          breadCrumbActive={`${this.formtype === 'edit' ? "Edit Assessment"  : "New Assessment"}`}
        />
        <div className='ml-1 mb-1'>
          <Button.Ripple outline color="info"  onClick={() => {this.props.history.push(`${subRooter}/assessment`)}}>
            <Feather.ArrowLeft size={20} />	Back to List
          </Button.Ripple>
        </div>

        <Card>
          <CardBody>
            <Form className='text-left' onSubmit={this.handleOnSubmit}  >
              <Row>
                <Col md='2' xs='12' xl='2' style={{zIndex: 1001}}>
                  <div className='m-auto'>
                    <FormGroup>
                      <h5 className="my-1 text-bold-500 text-center">Mark</h5>
                      <Select
                        className="React"
                        classNamePrefix="select"
                        name="mark"
                        options={markOptions}
                        value={markOptions.find((elt) => elt.value === mark)}
                        onChange={e => this.setState({mark: e.value})}
                      />
                    </FormGroup>
                  </div>
                </Col>
                <Col md='10' xs='12' xl='10'>
                  <Row>
                    <Col md='3' sm="12" style={{zIndex: 1000}}>
                      <FormGroup>
                        <Label for="level">Select Level</Label>
                        <Select
                          className="React"
                          classNamePrefix="select"
                          name="level"
                          id="level"
                          value={levelOptions.find((elt) => elt.value === selectedLevelId)}
                          options={levelOptions}
                          onChange={(e) => this.setState({
                            selectedLevelId: e.value,
                            selectedTopicId: ""
                          })}
                        />
                        {
                          !selectedLevelId && <p className='text-danger'>please select level!</p>
                        }
                      </FormGroup>
                    </Col>
                    <Col md='3' sm="12" style={{zIndex: 999}}>
                      <FormGroup>
                        <Label for="category">Select Category</Label>
                        {
                          console.log(categoryOptions.find((elt) => elt.value === selectedCategoryId))
                        }
                        {
                          console.log("selectedCategoryId", selectedCategoryId)
                        }
                        <Select
                          className="React"
                          classNamePrefix="select"
                          name="category"
                          id="category"
                          options={categoryOptions}
                          value={categoryOptions.find((elt) => elt.value === selectedCategoryId)}
                          onChange={(e) => this.setState({
                            selectedCategoryId: e.value,
                            selectedTopicId: ""
                          })}
                        />
                        {
                          !selectedCategoryId && <p className='text-danger'>please select level!</p>
                        }
                      </FormGroup>
                    </Col>
                    <Col md='5' sm="10" style={{zIndex: 998}}>
                      <FormGroup>
                        <Label for="topic">Select Topic</Label>
                        <Select
                          className="React"
                          classNamePrefix="select"
                          name="topic"
                          id="topic"
                          options={subjectOptions}
                          value={subjectOptions.find((elt) => elt.value === selectedTopicId)}
                          onChange={(e) => this.setState({
                            selectedTopicId: e.value
                          })}
                        />
                        {
                          !selectedTopicId && <p className='text-danger'>please select level!</p>
                        }
                      </FormGroup>
                    </Col>
                    <Col md='1' sm="2" className='mt-2'>
                      <FormGroup>
                        <Link to={`${subRooter}/topics`}>
                          <Feather.PlusCircle size={30}  />
                        </Link>
                      </FormGroup>
                    </Col>
                    <Col sm="12">
                      <FormGroup>
                        <Label for="question">Question</Label>
                        <Input
                          type="text"
                          name="question"
                          id="question"
                          placeholder="Input Question"
                          value={question}
                          onChange={(e) => this.setState({
                            question: e.target.value
                          })}
                          invalid={question === ""}
                        />
                        {
                          question === '' && <span className='invalid-tooltip'>please input name</span>
                        }
                      </FormGroup>
                    </Col>
                    <Col md='12' >
                        <Card>
                          <Label for="description" className='pb-1'>Description</Label>
                          <Editor id='description' html={description} onChange={(values) => this.handleEditor(values)}/>
                        </Card>
                      </Col>
                    {/* file upload form start */}
                    <Col md='12'>
                      <Row className='mb-1'>
                        <Col md='6'>
                          {
                            currentData && currentData.imgUrl ?
                            <Row className='m-1'>
                              <Col md='12'>
                                <img className='w-100' src={currentData.imgUrl} alt="image"	/>
                              </Col>
                              <Col md='12' className='text-right mt-1'>
                                <Button.Ripple
                                  color="danger"
                                  className="mb-1"
                                  onClick={() => {
                                    let temp = currentData
                                    temp.imgUrl = ""
                                    this.setState({
                                      currentData: temp
                                    })
                                  }}
                                >
                                  Remove
                                </Button.Ripple>
                              </Col>
                            </Row>
                          :
                          <>
                            <BasicDropZone 
                              accept="image/*" 
                              type="image"
                              placeholder="Image File" 
                              multiple={false}
                              onLoadFile={(files) => this.handleFiles('image', files)}
                              style={{background: 'linear-gradient(to right, rgb(86, 171, 47), rgb(168, 224, 99))'}}
                            />
                          </>
                          }
                        </Col>
                        <Col md='6'>
                          {
                            currentData && currentData.videos &&  currentData.videos.length > 0 && currentData.videos[0] !== "" ? 
                            <Row className='m-1'>
                              <Col md='12'>
                                <video controls
                                  src={currentData.videos[0]}
                                  width="620"
                                  className="w-100"
                                >
                                  Sorry, your browser doesn't support embedded videos, 
                                  but don't worry, you can download it
                                  and watch it with your favorite video player!
                                </video>
                              </Col>
                              <Col md='12' className='text-right'>
                                  <Button.Ripple
                                    color="danger"
                                    className="mb-1"
                                    onClick={() => {
                                      let temp = currentData
                                      temp.videos = []
                                      this.setState({
                                        currentData: temp
                                      })
                                    }}
                                  >
                                    Remove
                                  </Button.Ripple>
                              </Col>
                            </Row>
                            :
                            <BasicDropZone 
                              accept=".avi, .mp4, mpg, .ogg"
                              type="video"
                              placeholder="Video Files"
                              onLoadFile={(files) => this.handleFiles('video', files)}
                              style={{background: 'linear-gradient(to right, rgb(86, 171, 47), rgb(168, 224, 99))'}}
                            />
                          }
                        </Col>
                      </Row>
                    </Col>
                    {/* file upload form end */}
                    <Col sm="12">
                      <FormGroup>
                        <Label for="youtube-url">Youtube URL</Label>
                        <Input
                          type="text"
                          name="youtubeUrl"
                          id="youtube-url"
                          placeholder="Youtube URL"
                          value={youtubeUrl}
                          onChange={(e) => this.setState({
                            youtubeUrl: e.target.value  
                          })}
                          invalid={! /^(https?\:\/\/)?(www\.youtube\.com|youtu\.?be)\/.+$/.test(youtubeUrl) && youtubeUrl !== ''  && <p className='text-danger'>This is invalid youtube url</p>}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  {/* input answers start */}
                  <Row>
                    <Col>
                      <div className="d-inline-block mr-1">
                        <Checkbox
                          color="primary"
                          icon={<Check className="vx-icon" size={16} />}
                          label="MCQ"
                          checked={isMCQ}
                          value={this.state.isMCQ}
                          onChange={() => {
                            if(isMCQ) {
                              this.setState({isMCQ: !isMCQ })
                            } else {
                              this.setState({
                                isMCQ: !isMCQ,
                                MCQs: [
                                  {
                                    index: 0,
                                    isCorrect: false,
                                    text: ''
                                  }
                                ],
                              })
                            }
                          }}
                        />
                        {
                            console.log("this.state.isMCQ", this.state.isMCQ)
                          }
                      </div>
                      {/* <div className="d-inline-block mr-1">
                        <Checkbox
                          color="primary"
                          icon={<Check className="vx-icon" size={16} />}
                          checked={optionType === 'Written'}
                          label="Written"
                          onChange={() => this.handleChange("optionType", 'Written')}
                        />
                      </div>
                      <div className="d-inline-block mr-1">
                        <Checkbox
                          color="primary"
                          icon={<Mic className="vx-icon" size={16} />}
                          checked={optionType === 'Recording'}
                          label="Recording"
                          onChange={() => this.handleChange("optionType", 'Recording')}
                        />
                      </div> */}  
                    </Col>
                  </Row>
                  {
                    isMCQ && <Row>
                      {
                        ! MCQs.find((elt) => elt.isCorrect === true) && <p className='text-danger'>Please select correct answer</p>
                      }
                        <pre>
                        {
                          console.log("MCQs12312321", MCQs)
                        }
                      </pre>
                      <Col md='12'>
                        <FormGroup>
                          <InputGroup>
                            <Input 
                              value={MCQs[0].text}
                              onChange={(e) => this.handelChangeMCQValueByIndex(0, e.target.value)}
                            />
                            <InputGroupAddon addonType="append">
                              {/* <div className="d-inline-block mr-1"> */}
                                <Checkbox
                                  color="primary"
                                  icon={<Check className="vx-icon" size={16} />}
                                  label="Correct Answer"
                                  checked={MCQs[0].isCorrect}
                                  value={MCQs[0].isCorrect}
                                  onChange={() => this.checkMCQCorrectAnswer(0)}
                                  className='pl-1 bg-primary'
                                />
                              {/* </div> */}
                              <Button.Ripple color="success"
                                onClick={this.addNewMCQ}
                              >
                                <Feather.PlusSquare size={24} />
                              </Button.Ripple>
                            </InputGroupAddon>
                          </InputGroup>
                        </FormGroup>
                        {
                          MCQs.length	 > 1 && MCQs.map((MCQ, index) => {
                            if(index === 0) { return null; }
                            return (
                              <FormGroup key={index}>
                                <InputGroup>
                                  <Input value={MCQ.text} onChange={(e) => this.handelChangeMCQValueByIndex(index, e.target.value)} />
                                  <InputGroupAddon addonType="append">
                                    <Checkbox
                                      color="primary"
                                      icon={<Check className="vx-icon" size={16} />}
                                      label="Correct Answer"
                                      value={ MCQ.isCorrect }
                                      checked={MCQ.isCorrect }
                                      onChange={() => this.checkMCQCorrectAnswer(index)}
                                      className='pl-1 bg-primary'
                                    />
                                    <Button.Ripple color="danger"
                                      onClick={() => this.handleDeleteMCQByIndex(index)}
                                    >
                                      <Feather.Trash2 size={24} />
                                    </Button.Ripple>
                                  </InputGroupAddon>
                                </InputGroup>
                              </FormGroup>
                            )
                          })
                        }
                      </Col>
                    </Row>
                  }
                  {/* {
                    optionType === 'Recording' && <AudioRecorder />
                  } */}
                  {/* input answers end */}
                  {/* actions start */}
                  <Row>
                    <Col sm="12">
                      <FormGroup className='text-right'>
                        {
                          this.state.isInvalid && <p className='text-danger'>please check above fields</p>
                        }
                        <Button.Ripple
                          color="primary"
                          type="submit"
                          className="mr-1 mb-1"
                        >
                          Submit
                        </Button.Ripple>
                        {/* <Button.Ripple
                          outline
                          color="warning"
                          type="reset"
                          className="mb-1"
                        >
                          Reset
                        </Button.Ripple> */}
                      </FormGroup>
                    </Col>
                  </Row>
                  {/* actions end */}
                </Col>
              </Row>
            </Form>
          </CardBody>
        </Card>
      </React.Fragment>
		)
	}
}
const mapStateToProps = (state) => {
  console.log("state", state);
  
	return {
		levels: state.level.allData,
		categories: state.category.allData,
    topics: state.topic.dataPerType,
    allAssessments: state.assessment.allData,
    filteredData: state.assessment.queryData
	}
}

const mapDispatchToProps = {
	getLevels,
	getCategories,
	getTopics,
  updateData,
  addAssessment,
  getDatabyFilter
}

export default connect(mapStateToProps, mapDispatchToProps)(AssessmentForm)